.container {
    margin: 35px auto 0;
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  

  @media (max-width: 1573px) {
    .container {
      width: 50%;
    }
    .search {
      gap: 12px;
    }
    .search-top .location {
      font-size: 22px;
    }
    .search-bar i {
      right: 90px;
    }
    .weather-date {
      margin-top: 20px;
    }
    .weather-data .data-name,
    .weather-data .data,
    .weather-data i {
      font-size: 19px;
    }
    .current-weather-card {
      padding: 30px 100px;
    }
    .forecast-container {
      flex-direction: column;
    }
    .forecast-card {
      width: 100%;
      flex-direction: row;
      height: 50px;
      justify-content: space-between;
    }
    .forecast-card:first-child {
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;
    border-bottom-left-radius: 0px;
  
    }
  
    .forecast-card:last-child {
      border-bottom-right-radius: 12px;
      border-bottom-left-radius: 12px;
      border-top-right-radius: 0px;
  
    }
    .forecast-item {
      width: 25%;
    }
    .forecast-temp {
      font-size: 26px;
    }
    .forecast-type {
      font-size: 20px;
    }
  
  }
  @media (max-width: 1190px) {
    .container {
      width: 60%;
    }
    .current-weather-card {
      padding: 30px 70px;
    }
    .search-bar i {
      font-size: 18px;
    }
  }

  @media (max-width: 882px) {
    .container {
      width: 70%;
    }
    .current-weather-card {
      padding: 30px 55px;
    }
  }
  @media (max-width: 692px) {
    .container {
      width: 80%;
    }
  }

