@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
}


body {
  background-image: url(/weather-forecast/static/media/weather.bb1c665c.jpg);
  background-repeat: no-repeat;
  /* background-position: center; */
  background-size: cover;
  /* height: 100vh;
  overflow: hidden; */
}


.current-weather-card {
    width: 100%;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 130px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .search {
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-row-gap: 20px;
    row-gap: 20px;
  }
  
  .search-top {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column-gap: 12px;
    column-gap: 12px;
  }
  
  .search-top i {
    font-size: 28px;
    color: #f38383;
  }
  
  .search-top .location {
    font-size: 24px;
    color: #fff;
    letter-spacing: 2px;
  }
  
  .search-bar {
    display: flex;
    justify-content: center;
    position: relative;
  }
  
  .search-bar input {
    width: 70%;
    height: 38px;
    background-color: transparent;
    border: 1px solid #e4e2e2;
    border-radius: 32px;
    outline: none;
    padding-left: 20px;
    font-size: 20px;
    color: #393636;
  }
  
  .search-bar input::placeholder {
    font-size: 16px;
    color: #9f9797;
    letter-spacing: 2px;
  }
  
  .search-bar input:focus::placeholder {
    color: transparent;
  }
  
  .search-bar i {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 130px;
    font-size: 20px;
    color: #393636;
    cursor: pointer;
  }
  
  .forecast-option {
    display: flex;
    justify-content: center;
    grid-gap: 35px;
    gap: 35px;
    margin-top: 10px;
  }
  
  .forecast-option label {
    font-size: 16px;
    color: #393636;
  }
  
  .forecast-input {
    margin-right: 5px;
  }
  
  .weather-date {
    font-size: 28px;
    color: #495e7b;
    margin-top: 30px;
  }
  
  .weather {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .weather img {
    width: 170px;
  }
  
  .weather-type {
    font-size: 24px;
    color: #2f2e57;
    margin-top: 15px;
  }
  
  .temp {
    font-size: 110px;
    background-image: linear-gradient(to right, #2f2e57, #605d80);
    -webkit-background-clip: text;
            background-clip: text;
    color: transparent;
    margin-top: 15px;
  }
  
  .weather-data {
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-top: 30px;
  }
  
  .weather-wrap {
    text-align: left;
  }
  
  .weather-data .humidity,
  .weather-data .wind {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-row-gap: 6px;
    row-gap: 6px;
  }
  
  .weather-data i {
    font-size: 24px;
    color: #2f2e57;
  }
  
  .weather-data .data-name {
    font-size: 24px;
    color: #2f2e57;
  }
  
  .weather-data .data {
    font-size: 28px;
    color: #2f2e57;
  }
  
  .min-max-temp {
    margin-top: 10px;
  }
  
  .min-max-data {
    color: #2f2e57;
    margin-right: 10px;
  }
  
  .error-message {
    color: red;
    font-size: 26px;
    margin-top: 15px;
  }
.forecast-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 12px 0;
  }
  
  .forecast-card {
    height: 208px;
    background-color: rgba(255, 255, 255, 0.5);
    width: 25%;
    padding: 12px 5px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .forecast-card:first-child {
    border-top-left-radius: 12px;
    border-bottom-left-radius: 12px;
  }
  
  .forecast-card:last-child {
    border-top-right-radius: 12px;
    border-bottom-right-radius: 12px;
  }
  
  .forecast-card-hour img {
    width: 60px;
  }
  
  .forecast-card img {
    width: 50px;
    height: 50px;
  }
  
  .forecast-date {
    font-size: 16px;
    color: #2f2e57;
    margin-bottom: 10px;
  }
  
  .forecast-temp {
    font-size: 32px;
    color: #2f2e57;
    margin: 10px 0;
  }
  
  .forecast-type {
    font-size: 22px;
    color: #2f2e57;
  }
.container {
    margin: 35px auto 0;
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  

  @media (max-width: 1573px) {
    .container {
      width: 50%;
    }
    .search {
      grid-gap: 12px;
      gap: 12px;
    }
    .search-top .location {
      font-size: 22px;
    }
    .search-bar i {
      right: 90px;
    }
    .weather-date {
      margin-top: 20px;
    }
    .weather-data .data-name,
    .weather-data .data,
    .weather-data i {
      font-size: 19px;
    }
    .current-weather-card {
      padding: 30px 100px;
    }
    .forecast-container {
      flex-direction: column;
    }
    .forecast-card {
      width: 100%;
      flex-direction: row;
      height: 50px;
      justify-content: space-between;
    }
    .forecast-card:first-child {
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;
    border-bottom-left-radius: 0px;
  
    }
  
    .forecast-card:last-child {
      border-bottom-right-radius: 12px;
      border-bottom-left-radius: 12px;
      border-top-right-radius: 0px;
  
    }
    .forecast-item {
      width: 25%;
    }
    .forecast-temp {
      font-size: 26px;
    }
    .forecast-type {
      font-size: 20px;
    }
  
  }
  @media (max-width: 1190px) {
    .container {
      width: 60%;
    }
    .current-weather-card {
      padding: 30px 70px;
    }
    .search-bar i {
      font-size: 18px;
    }
  }

  @media (max-width: 882px) {
    .container {
      width: 70%;
    }
    .current-weather-card {
      padding: 30px 55px;
    }
  }
  @media (max-width: 692px) {
    .container {
      width: 80%;
    }
  }


